.aiot-theme-wordpress {
  border-right: 1px solid #e5e5e5; }
  .aiot-theme-wordpress > .aiot-pad.wrap {
    margin: 0 0 15px 0; }
  .aiot-theme-wordpress .aiot-tree-headline {
    height: 31px;
    font-size: 23px;
    font-weight: 400;
    margin: 0;
    padding: 9px 15px 4px 0;
    line-height: 29px; }
  .aiot-theme-wordpress .aiot-nodes {
    padding-bottom: 80px; }
    .aiot-theme-wordpress .aiot-nodes .aiot-node-icon i {
      background: #f1f1f1 !important;
      border-top: 3px solid #f1f1f1 !important;
      border-bottom: 3px solid #f1f1f1 !important; }

body.activate-aiot {
  position: static !important; }
  body.activate-aiot #wpbody-content {
    float: right;
    box-sizing: border-box;
    padding-left: 15px; }
  body.activate-aiot.rtl #wpbody-content {
    padding-left: 0; }
  body.activate-aiot.rtl .aiot-theme-wordpress {
    border-right: 1px solid #e5e5e5; }
    body.activate-aiot.rtl .aiot-theme-wordpress > .aiot-pad.wrap {
      margin-left: 15px; }

/* Dark Mode */
body.aiot-wp-dark-mode .aiot-theme-wordpress {
  border-right-color: #535556; }

body.aiot-wp-dark-mode .aiot-tree .aiot-split-collapse, body.aiot-wp-dark-mode .aiot-tree .aiot-split-resizer {
  background: #464646; }

body.aiot-wp-dark-mode .aiot-tree .aiot-split-collapse:after, body.aiot-wp-dark-mode .aiot-tree .aiot-split-resizer:after {
  color: #9a9a9a; }

body.aiot-wp-dark-mode .aiot-tree .aiot-nodes .aiot-node input.aiot-node-name,
body.aiot-wp-dark-mode .aiot-tree .aiot-toolbar-items > a:hover {
  color: white; }

body.aiot-wp-dark-mode .aiot-tree .aiot-fixed-header > div {
  background: #23282d; }
  body.aiot-wp-dark-mode .aiot-tree .aiot-fixed-header > div .aiot-toolbar {
    border-color: #191f25; }

body.aiot-wp-dark-mode .aiot-toolbar {
  background-color: #32373c;
  border-color: #191f25;
  color: #bbc8d4; }

body.aiot-wp-dark-mode .aiot-tree .aiot-nodes .aiot-node {
  color: #bbc8d4; }
  body.aiot-wp-dark-mode .aiot-tree .aiot-nodes .aiot-node .aiot-node-count {
    background: #3e464e; }
  body.aiot-wp-dark-mode .aiot-tree .aiot-nodes .aiot-node.aiot-active {
    background: #32383e;
    background: linear-gradient(to right, rgba(224, 224, 224, 0) 30px, #23282d 30%, #4e555d 100%); }

body.aiot-wp-dark-mode .aiot-tree hr {
  border-color: #454c53; }

body.aiot-wp-dark-mode .aiot-tree .aiot-expander {
  color: #9ea3a8; }

body.aiot-wp-dark-mode .aiot-tree .aiot-nodes ul:after {
  border-right-color: #616161;
  border-left-color: #424242; }

body.aiot-wp-dark-mode .aiot-tree .aiot-expander:after {
  background: #23282d; }

body.aiot-wp-dark-mode .aiot-theme-wordpress .aiot-nodes .aiot-node-icon i {
  background-color: #23282d !important;
  border-top-color: #23282d !important;
  border-bottom-color: #23282d !important; }

/* Respnsiveness */
@media all and (max-width: 700px) {
  .aiot-tree.aiot-full-width .aiot-nodes {
    padding-bottom: 0; }
  #wpbody-content {
    width: auto !important;
    float: inherit !important;
    display: block !important;
    left: inherit !important;
    right: inherit !important;
    bottom: inherit !important;
    top: inherit !important; } }

/* Material WP */
@media only screen and (min-width: 700px) {
  body.aiot-wp-material.activate-aiot:not(.wp-customizer) #wpbody-content {
    margin-left: 0px !important; }
  body.aiot-wp-material.activate-aiot .aiot-tree {
    margin-top: 30px; }
    body.aiot-wp-material.activate-aiot .aiot-tree .aiot-split-collapse, body.aiot-wp-material.activate-aiot .aiot-tree .aiot-split-resizer {
      z-index: 99; }
    body.aiot-wp-material.activate-aiot .aiot-tree .page-title-action {
      padding-left: 10px !important;
      padding-right: 10px !important; }
    body.aiot-wp-material.activate-aiot .aiot-tree .aiot-fixed-header:not(.active) > div {
      background: none; }
      body.aiot-wp-material.activate-aiot .aiot-tree .aiot-fixed-header:not(.active) > div .aiot-tree-headline {
        color: white;
        text-shadow: 1px 1px 1px #0000003b; }
      body.aiot-wp-material.activate-aiot .aiot-tree .aiot-fixed-header:not(.active) > div .aiot-toolbar {
        background: none;
        color: white;
        border: 0; }
        body.aiot-wp-material.activate-aiot .aiot-tree .aiot-fixed-header:not(.active) > div .aiot-toolbar .aiot-toolbar-items > a {
          color: white; } }

body.aiot-wp-material.activate-aiot.mwp-expanded #wpbody > .rml-container {
  margin-left: 15px !important; }

body.aiot-wp-material.activate-aiot.mwp-expanded #wpbody > #wpbody-content {
  left: initial !important; }

@media only screen and (max-width: 699px) {
  body.aiot-wp-material.activate-aiot .rml-container {
    margin-left: 0px !important;
    float: none;
    background: #ececec; }
    body.aiot-wp-material.activate-aiot .rml-container .aiot-pad {
      min-height: auto; }
    body.aiot-wp-material.activate-aiot .rml-container .aiot-tree {
      margin: 0 5% !important; } }
